	<div>
		<div class="top">
			<div class="background"></div>
			<div class="svg_box">
				<canvas id="myCanvas1" height="200"></canvas>
				<canvas id="myCanvas2" height="200"></canvas>

			</div>

			<div class="avatar">
				<p>{{list.realName}}</p>
			</div>

		</div>

		<div v-if="list.declareSwitch==1" class="item" style="margin-top: 0.8rem;" @click="showError">
			<div class="left">
				<i style="background: url(../../../static/home_1.png) no-repeat;background-size:100% 100% ;"></i>
				<span>入户申报</span>

			</div>

			<div class="right">
				<span style="color: #666666;">未开通</span>

				<i></i>
			</div>

		</div>

		<router-link v-if="list.declareSwitch==2" class="item" style="margin-top: 0.8rem;">
			<div class="left">
				<i style="background: url(../../../static/home_1.png) no-repeat;background-size:100% 100% ;"></i>
				<span>入户申报</span>

			</div>

			<div class="right">

				<span>已开通</span>
				<i></i>
			</div>

		</router-link>

		<router-link :to="{ name: 'Applyforexamination' }" v-if="list.applySwitch==2" class="item">
			<!--	<div class="item" @click="showError">-->
			<div class="left">
				<i style="background: url(../../../static/home_2.png) no-repeat;background-size:100% 100% ;"></i>
				<span>报考申请</span>

			</div>

			<div class="right">
				<span>已开通</span>
				<i></i>
			</div>
			<!--</div>-->
		</router-link>
		
		<div class="item" v-if="list.applySwitch==1" @click="showError">
			<div class="left">
				<i style="background: url(../../../static/home_2.png) no-repeat;background-size:100% 100% ;"></i>
				<span>报考申请</span>

			</div>

			<div class="right">
				<span style="color: #666666;">未开通</span>
				<i></i>
			</div>
			</div>
		
		
		
		
		
		
		<router-link v-if="list.trainSwitch==2" :to="{ name: 'StudyHome' }" class="item">
			<div class="left">
				<i style="background: url(../../../static/home_3.png) no-repeat;background-size:100% 100% ;"></i>
				<span>学习园地</span>

			</div>

			<div class="right">
				<span>已开通</span>
				<i></i>
			</div>

		</router-link>
 
         <div v-if="list.trainSwitch==1" class="item" @click="showError">
         	<div class="left">
				<i style="background: url(../../../static/home_3.png) no-repeat;background-size:100% 100% ;"></i>
				<span>学习园地</span>

			</div>

			<div class="right">
				<span style="color: #666666;">未开通</span>
				<i></i>
			</div>
         </div>







		<div class="item" @click="showError">
			<div class="left">
				<i style="background: url(../../../static/home_4.png) no-repeat;background-size:100% 100% ;"></i>
				<span>平台公告</span>

			</div>

			<div class="right">
				<span style="color: #fa8a12;" v-if="list.unRead>0">{{list.unRead}}</span>
				<i></i>
			</div>

		</div>
		<!--	<router-link :to="{ name: 'Progressquery' }" class="item">-->
		<div class="item" @click="showError">
			<div class="left">
				<i style="background: url(../../../static/home_5.png) no-repeat;background-size:100% 100% ;"></i>
				<span>进度查询</span>

			</div>

			<div class="right">
				<span style="color: #fa8a12;" v-if="list.progressUpdate==2">有新的进度</span>
				<i></i>
			</div>
		</div>

		<!--</router-link>-->
		<div class="item" @click="showError">
			<div class="left">
				<i style="background: url(../../../static/home_6.png) no-repeat;background-size:100% 100% ;"></i>
				<span>意见反馈</span>

			</div>

			<div class="right">

				<i></i>
			</div>

		</div>

		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default {
		components: {
			Bottom
		},
		data() {
			return {
				font: this.$store.state.font,
				list: {}
			}
		},
		created() {
			this.getData()
		},
		mounted() {
			var canvas1 = document.getElementById('myCanvas1');
			canvas1.width = window.innerWidth;
			var canvas2 = document.getElementById('myCanvas2');
			canvas2.width = window.innerWidth;
			this.wave1()
			this.wave2()
		},
		methods: {
			wave1() {
				var waveWidth = 3300,
					offset = -60,
					waveHeight = 40, //波浪高度
					waveCount = 8, //波浪个数
					startX = -1000,
					startY = 250, //canvas 高度
					progress = 90, //波浪位置的高度
					d2 = waveWidth / waveCount,
					d = d2 / 2,
					hd = d / 2,
					c = document.getElementById("myCanvas1"),
					ctx = c.getContext("2d");

				function tick() {

					if(-1 * offset === d2) offset = 0;
					ctx.clearRect(0, 0, c.width, c.height);
					ctx.fillStyle = 'rgba(255, 255, 255, 0.5)';
					ctx.beginPath();
					var offsetY = startY - progress;
					ctx.moveTo(startX - offset, offsetY);
					for(var i = 0; i < waveCount; i++) {
						var dx = i * d2;
						var offsetX = dx + startX - offset;
						ctx.quadraticCurveTo(offsetX + hd, offsetY + waveHeight, offsetX + d, offsetY);
						ctx.quadraticCurveTo(offsetX + hd + d, offsetY - waveHeight, offsetX + d2, offsetY);
					}
					ctx.lineTo(startX + waveWidth, 3000);
					ctx.lineTo(startX, 3000);
					ctx.fill();

					setTimeout(tick, 1500 / 60); //速度
					//requestAnimationFrame(tick);
				}
				tick();
			},
			wave2() {
				var waveWidthB = 3300,
					offsetB = 180,
					waveHeightB = 50, //波浪高度
					waveCountB = 8, //波浪个数
					startXB = -500,
					startYB = 250, //canvas 高度
					progressB = 90, //波浪位置的高度
					d2B = waveWidthB / waveCountB,
					dB = d2B / 2,
					hdB = dB / 2,
					cB = document.getElementById("myCanvas2"),
					ctxB = cB.getContext("2d");

				function tickB() {

					if(-1 * offsetB === d2B) offsetB = 0;
					ctxB.clearRect(0, 0, cB.width, cB.height);
					ctxB.fillStyle = 'white';
					ctxB.beginPath();
					var offsetYB = startYB - progressB;
					ctxB.moveTo(startXB - offsetB, offsetYB);
					for(var i = 0; i < waveCountB; i++) {
						var dxB = i * d2B;
						var offsetXB = dxB + startXB - offsetB;
						ctxB.quadraticCurveTo(offsetXB + hdB, offsetYB + waveHeightB, offsetXB + dB, offsetYB);
						ctxB.quadraticCurveTo(offsetXB + hdB + dB, offsetYB - waveHeightB, offsetXB + d2B, offsetYB);
					}
					ctxB.lineTo(startXB + waveWidthB, 3000);
					ctxB.lineTo(startXB, 3000);
					ctxB.fill();

					setTimeout(tickB, 5000 / 60); //速度
					//requestAnimationFrame(tick);
				}
				tickB();

			},

			showError() {
				this.$toast.center('正在建设中，敬请期待');

			},
			getData() {

				var obj = {
					"s": this.$store.state.sessionId,
					"a": 1

				}
				this.$Api.UserDetails(obj).then((res) => {
					console.log(res)
					if(res.q.d!="操作成功"){
						this.$toast.center('您还未登录');
						this.$router.push({
							name:"Binding"
							
						})
						
					}else{
						this.list = res.q
						
					}
				})

			}

		}

	}
</script>

<style scoped="scoped" lang="scss">
	.top {
		height: 3.5rem;
		width: 100%;
		position: relative;
		.background {
			width: 100%;
			height: 3.2rem;
			background: url(../../assets/background.jpg) no-repeat;
			background-size: 100% 100%;
		}
		.svg_box {
			height: 200px;
			width: 100%;
			position: absolute;
			bottom: 0.3rem;
			z-index: 999;
			canvas {
				position: absolute;
				top: 0;
				left: 0;
			}
			#myCanvas1 {
				z-index: 1;
			}
			#myCanvas2 {
				z-index: 2;
			}
		}
		.avatar {
			width: 1.55rem;
			height: 1.55rem;
			background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1522300298011&di=e4b387e3c04d878c58d6cb54385a73a6&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2014-09-23%2F000247589.jpg) no-repeat;
			background-size: 100% 100%;
			border-radius: 50%;
			position: absolute;
			bottom: 0.5rem;
			z-index: 99999;
			left: 0.6rem;
			p {
				position: absolute;
				width: 100%;
				text-align: center;
				font-size: 0.32rem;
				bottom: -0.8rem;
				color: #000000;
			}
		}
	}
	
	.item {
		float: left;
		width: 100%;
		height: 0.9rem;
		border-bottom: 0.02rem solid #CCCCCC;
		font-size: 0.3rem;
		line-height: 0.9rem;
		color: #000000;
		.left {
			float: left;
			i {
				width: 0.4rem;
				height: 0.38rem;
				margin-left: 0.3rem;
				position: relative;
				top: 0.25rem;
				float: left;
				margin-right: 0.2rem;
			}
			span {}
		}
		.right {
			float: right;
			margin-right: 0.3rem;
			i {
				width: 0.13rem;
				height: 0.23rem;
				position: relative;
				top: 0.3rem;
				margin-left: 0.2rem;
				float: right;
				background: url(../../assets/go.png) no-repeat;
				background-size: 100% 100%;
			}
		}
	}
</style>


